<template>
  <div>
    <div class="send-content">
      <el-form ref="ruleForm" label-width="100px" :model="value">
        <el-form-item
          label=""
          prop="title"
          :rules="[{ required: true, message: '', trigger: 'blur' }]"
        >
          <el-input v-model="value.title" maxlength="20" show-word-limit />
        </el-form-item>
        <el-form-item
          v-if="currentTask.subType === 4 || currentTask.subType === 2"
          label=""
          prop="desc"
        >
          <el-input
            v-model="value.desc"
            type="textarea"
            maxlength="100"
            show-word-limit
            rows="3"
          />
        </el-form-item>
        <el-form-item
          label=""
          prop="content"
          :rules="[
            { required: true, message: '', trigger: 'change' }
          ]"
        >
          <vue-editor
            ref="editor"
            v-model="value.content"
            :use-custom-image-handler="true"
            placeholder=""
            @image-added="handleImageAdded"
          />
        </el-form-item>
        <el-form-item
          v-if="currentTask.delayDay && currentTask.delayDay > 0"
          label=""
          :rules="[
            { required: true, message: '', trigger: 'blur' }
          ]"
          prop="delayReason"
        >
          <el-input
            v-model="value.delayReason"
            placeholder=""
            rows="5"
            maxlength="100"
            show-word-limit
            type="textarea"
          />
        </el-form-item>
        <!---->
        <el-form-item>
          <video-uploader
            ref="videoUploader"
            :handle-change="handleChange"
            :upload-file-func="uploadFileFunc"
            :video-remove="videoRemove"
            :handle-file-error="handleFileError"
            :upload-loading="uploadLoading"
            :fileList="fileList"
            :file-url-list="value.contentVideos"
          ></video-uploader>
        </el-form-item>
      </el-form>
    </div>
    <div class="button-container">
      <el-button :disabled="uploadLoading" @click="handleClose"
        ></el-button
      >
      <el-button :disabled="uploadLoading" @click="handleSave"></el-button>
      <el-button type="primary" :disabled="uploadLoading" @click="handlePreview"
        ></el-button
      >
    </div>
  </div>
</template>

<script>
import { VueEditor } from 'vue2-editor'
import { upload } from '@/api/upload'
import VideoUploader from './common/VideoUploader'

export default {
  name: 'SendContentForm',
  components: {
    VueEditor,
    VideoUploader
  },
  props: {
    // value
    value: {
      type: Object,
      default: () => {}
    },
    currentTask: {
      type: Object,
      default: () => {}
    }
  },
  data() {
    return {
      uploadLoading: false,
      fileList: []
    }
  },
  methods: {
    handleFileError() {
      this.uploadLoading = false
    },
    // 
    handleChange(file, fileList) {
      this.fileList = fileList
    },
    // 
    videoRemove(file, fileList) {
      const removeIndex = this.fileList.findIndex(
        targetfile => targetfile.uid === file.uid
      )
      this.value.contentVideos.splice(removeIndex, 1)
    },
    // 
    async uploadFileFunc(option) {
      this.uploadLoading = true
      const file = option.file
      const { url } = await upload(file, progress => {})
      const name = file.name
      const videoObj = {
        url,
        name
      }
      this.value.contentVideos.push(videoObj)
      this.uploadLoading = false
    },
    // 
    async handleImageAdded(file, Editor, cursorLocation, resetUploader) {
      // 
      const data = await upload(file)
      // url  
      Editor.insertEmbed(cursorLocation, 'image', encodeURI(data.url))
      resetUploader()
      // 
      if (!this.value.contentPic) {
        this.value.contentPic = encodeURI(data.url)
      }
    },
    handleClose() {
      this.$emit('handleClose')
    },
    handleSave() {
      this.$refs.ruleForm.validate(valid => {
        if (valid) {
          this.$emit('handleSave')
        } else {
          return false
        }
      })
    },
    handlePreview() {
      this.$refs.ruleForm.validate(valid => {
        if (valid) {
          this.$emit('preview')
        } else {
          return false
        }
      })
    }
  }
}
</script>

<style scoped>
.button-container {
  text-align: center;
}
</style>
